import {
    DEFAULT_TOAST_OPTIONS,
    Toast,
    ToastOptions,
    ToastPosition,
    ToastType,
} from '../types/toast';
import { type AtToasterComponent } from '../components/at-toaster/at-toaster';

export class ToasterService {
    private static id = 0;
    private static containers: Map<ToastPosition, AtToasterComponent> =
        new Map();

    /**
     * Show a toast message
     *
     * @param type The type of the toast
     * @param message The message to display in the toast
     * @param options Additional options for the toast (title, position, timeout, dismissible)
     */
    static async show(
        type: ToastType,
        message: string,
        options: ToastOptions = {},
    ): Promise<void> {
        const id = this.id++;
        const toastOptions: ToastOptions = {
            ...DEFAULT_TOAST_OPTIONS,
            ...options,
        };
        const toast: Toast = { id, type, message, ...toastOptions };
        this.getToaster(toastOptions.position).addToast(toast);
    }

    /**
     * Get or create at-toaster component
     * Stores the toaster container with position in the Map
     */
    private static getToaster(position: ToastPosition): AtToasterComponent {
        if (this.containers.has(position)) {
            return this.containers.get(position);
        }
        const el = document.createElement('at-toaster');
        el.setAttribute('position', position);
        document.body.appendChild(el);

        const toasterEl = el as unknown as AtToasterComponent;
        this.containers.set(position, toasterEl);

        return toasterEl;
    }
}
